body {
  --hover-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header__flex-gen-container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.flex-gen-container__img-container {
  margin-top: 2px;
}

.nav-container__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-container__nav-list li {
  justify-content: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}
